	module.exports = {"data":{"site":{"siteMetadata":{"title":"lasse.tech","siteUrl":"https://lasse.tech"}},"markdownRemark":{"excerpt":"Twitter doesn't use React on their regular desktop site, at least that is what I thought. But today I noticed my React Developer Tool icon…","html":"<p>Twitter doesn't use React on their regular desktop site, at least that is what I thought. But today I noticed my React Developer Tool icon in Chrome light up occasionally when browsing Twitter. Twitter load by default 4 .js files, some of these might be translation files. They take up a 500KB when compressed. Now, React version 16.2.0 won't load unless you have a video element in the stream. When you scroll to a video element Twitter loads additionally 5 files and the React Developer icon will light up. These 5 compressed files takes up 280KB (they could be minimized for production too). This looks like this:</p>\n\n  <a class=\"gatsby-resp-image-link\" href=\"/static/screenshot.twitter-b12f51094d5c0e15a36714d254bba9cc-b7a52.png\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 600px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 107.66182298546896%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"Screenshot Twitter ReactJS\" title=\"\" src=\"/static/screenshot.twitter-b12f51094d5c0e15a36714d254bba9cc-03757.png\" srcset=\"/static/screenshot.twitter-b12f51094d5c0e15a36714d254bba9cc-2c926.png 150w,\n/static/screenshot.twitter-b12f51094d5c0e15a36714d254bba9cc-94e25.png 300w,\n/static/screenshot.twitter-b12f51094d5c0e15a36714d254bba9cc-03757.png 600w,\n/static/screenshot.twitter-b12f51094d5c0e15a36714d254bba9cc-d6800.png 900w,\n/static/screenshot.twitter-b12f51094d5c0e15a36714d254bba9cc-e43f3.png 1200w,\n/static/screenshot.twitter-b12f51094d5c0e15a36714d254bba9cc-b7a52.png 1514w\" sizes=\"(max-width: 600px) 100vw, 600px\">\n    </span>\n  </span>\n  \n  </a>\n    \n<p>A few code snippets from the loaded javascript files:</p>\n<div class=\"gatsby-highlight\" data-language=\"javascript\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\">la<span class=\"token punctuation\">.</span><span class=\"token function\">injectIntoDevTools</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n  findFiberByHostInstance<span class=\"token punctuation\">:</span> w<span class=\"token punctuation\">,</span>\n  bundleType<span class=\"token punctuation\">:</span> <span class=\"token number\">0</span><span class=\"token punctuation\">,</span>\n  version<span class=\"token punctuation\">:</span> <span class=\"token string\">'16.2.0'</span><span class=\"token punctuation\">,</span>\n  rendererPackageName<span class=\"token punctuation\">:</span> <span class=\"token string\">'react-dom'</span><span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span></code></pre>\n      </div>\n<div class=\"gatsby-highlight\" data-language=\"javascript\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token keyword\">function</span> <span class=\"token constant\">I</span><span class=\"token punctuation\">(</span>e<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token constant\">L</span> <span class=\"token operator\">=</span> e<span class=\"token punctuation\">.</span>getFiberCurrentPropsFromNode<span class=\"token punctuation\">,</span> <span class=\"token constant\">N</span> <span class=\"token operator\">=</span> e<span class=\"token punctuation\">.</span>getInstanceFromNode<span class=\"token punctuation\">,</span> <span class=\"token constant\">D</span> <span class=\"token operator\">=</span> e<span class=\"token punctuation\">.</span>getNodeFromInstance\n  <span class=\"token punctuation\">}</span>\n  <span class=\"token comment\">/** @license React v16.2.0</span></code></pre>\n      </div>","frontmatter":{"date":"5 July, 2018","path":"/twitter-uses-reactjs-16.2.0-for-video-elements/","title":"Twitter uses ReactJS 16.2.0 for video elements","tags":["React","Twitter"]}}},"pathContext":{}}

